import React, { Component } from 'react'
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import { Divider } from '@material-ui/core';
import a from './Aditya.jpg';
import Avatar from '@material-ui/core/Avatar';
export default class Intro extends Component {
    render() {
        return (
            <div>
                <Paper className="about">
                    <div><Avatar  variant="rounded" alt="" src={a} className="avatar" ></Avatar></div><div className="intro"><b> I am Aditya!</b></div>
                    
                    <div className = "firstpara">Born and brought up in India. I pursued my bachelor's degree in Computer Engineering from the University of Mumbai. After graduation, I worked at Capgemini as an Associate Consultant. I have accumulated expertise in SAP Business Warehousing, where I performed daily Data loading and Data migration. 
</div >

<div className = "firstpara">I decided to pursue my master's in Information Systems at Stevens Institute of Technology here in New Jersey and have grown immense interest in Python and its Application. I acquired expertise in programming foundations in Python and developed projects in Machine Learning and Artificial Intelligence.  I freshly began learning ReactJS, Firebase, and Flask for developing Full Stack Web Applications together with Data Analytics and Visualization.
</div>

<div className = "firstpara">Aloof from academics, I have a piercing interest in Dance, Cooking, and Football. I performed in several dance competitions. I represented my college football team in my bachelor's.  
</div>

<div className = "firstpara">Values I live by</div>
<div className = "firstpara">
There is always an opportunity in adversity.</div>
<div className = "firstpara">The most crucial aspect of succeeding in a career is to add value to the people with who you work.
</div>
<div className = "firstpara"> The road to success and feeling satisfied is not in achieving the goal always but to enjoy the process.
</div>
<div className = "firstpara">Expressing gratitude and appreciation for what we have in present will provide us better in future.
</div>
                </Paper>
               




            </div>
        )
    }
}
